.Tracker {
    width: 70px;
    margin-left: 50px;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.Tracker__Item {
    width: 100%;
    height: 30px;
    font-weight: bold;
    font-size: 22px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.Tracker__Item:not(:last-child) {
    margin-bottom: 15px;
}

.Tracker__Tab {
    width: 10px;
    height: 30px;
    background-image: linear-gradient(to bottom right, var(--blue-grad-start), var(--blue-grad-end));
    border-radius: 50px;
}

.Tracker__Item--Active {
    color: var(--grey-dark);
}

.Tracker__Item--Correct {
    color: var(--accent-green);
}

.Tracker__Item--Incorrect {
    color: var(--accent-red);
}

.Tracker__Item--Unvisited {
    color: var(--grey-mid);
}
